.dash__statistic {
	display: flex;
	margin: 32px;
	padding: 1rem 0rem;
	justify-content: center;
	align-items: center;

	border-radius: 1rem;
	background: var(--color-old-secondary-colors-slate-main-slate-ffffff, #fff);

	/* Effect card light */
	box-shadow: 0px 12px 24px -4px rgba(145, 158, 171, 0.12),
		0px 0px 2px 0px rgba(145, 158, 171, 0.2);
}

.dash__statistic__divider {
	width: 1px;
	height: 100%;
	border-right: 0.5px rgba(145, 158, 171, 0.24) dashed;
}
